import React, {useState, useEffect} from 'react';
import styled from 'styled-components';
import {FaBars} from 'react-icons/fa';

const Nav = styled.nav`
  width: 100vw;
  height: 80px;
  background: black;
  opacity: ${({ scrollNav }) => (scrollNav ? '0.75' : '1')};
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;`

const NavbarContainer = styled.div`
width: 90%;
height: 100%;
margin: 0 auto;
display: -webkit-box;
display: -ms-flexbox;
display: flex;
-webkit-box-pack: justify;
    -ms-flex-pack: justify;
        justify-content: space-between;
-webkit-box-align: center;
    -ms-flex-align: center;
        align-items: center;`

const NavLogo = styled.p`
    font-size: 1.8rem;
    color: white;
    cursor: pointer;`

const MobileIcon = styled.div`
    color: white;
    cursor:pointer;
    font-size: 1.8rem;`

function scrollUp(e) {
    e.preventDefault();
    window.scrollTo({top:0, behavior:'smooth'});
}


const Navbar = ({toggle}) => {

    const [scrollNav, setScrollNav] = useState(false)

    const changeNav = () => {
        if(window.scrollY > 500) {
            setScrollNav(true)
        } else {
            setScrollNav(false)
        }
    }

    useEffect(() => {
        window.addEventListener('scroll', changeNav)
    }, [])

    return (
        <>
          <Nav scrollNav={scrollNav}>
            <NavbarContainer>
                <NavLogo onClick={scrollUp}><img id="monLogo" src={process.env.PUBLIC_URL + './logo-rlpz.png'} alt="RLPZ"></img></NavLogo>
                <MobileIcon onClick={toggle}>
                    <FaBars />
                </MobileIcon>
            </NavbarContainer>
          </Nav>
        </>
    )
}

export default Navbar