import React from 'react';
import styled from 'styled-components';
import {FaTimes} from 'react-icons/fa';

const Sidebarcontainer = styled.aside`
    position: fixed;
    z-index: 999;
    width: 100%;
    height: 100vh;
    background: black;
    display: grid;
    grid-template-rows: 80px auto;
    align-items: center;
    top: 0;
    left: 0;
    transition: 750ms ease-in-out;
    opacity: ${({ isOpen }) => (isOpen ? '100%' : '0')};
    top: ${({ isOpen }) => (isOpen ? '0' : '-100%')};
    transform: ${({ isOpen }) => (isOpen ? 'translateY(0)' : 'translateY(-100%)')}`

const CloseIcon = styled(FaTimes)`
    color: white;
    
    &&:hover{
        -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
        -webkit-transition: -webkit-transform 500ms ease-in-out;
        transition: -webkit-transform 500ms ease-in-out;
    }`

const Icon = styled.div`
    width: 90%;
    margin: 0 auto;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 70px;
    font-size: 1.8rem;
    cursor: pointer;`

const SidebarWrapper = styled.div`
  height: 80vh;
  width: auto;

  background-color: black;
  display: -ms-grid;
  display: grid;
  -ms-grid-rows: auto;
      grid-template-rows: auto;
  -ms-grid-columns: 100%;
      grid-template-columns: 100%;
      justify-items: center;
      align-items: flex-start;
`

const SidebarLinks = styled.a`
    text-decoration: none;
    letter-spacing: 0.2rem;
    color: transparent;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: white;
    font-size: 36px;
    font-weight: 600;
    cursor: pointer;
    
    &&:hover {
        -webkit-text-stroke-width: 2px;
        -webkit-text-stroke-color: red;
        border-bottom: 3px solid red;
    }`


const Sidebar = ({isOpen, toggle}) => {
    return (
        <Sidebarcontainer isOpen={isOpen} onClick={toggle}>
            <Icon onClick={toggle}>
                <CloseIcon />
            </Icon>
            <SidebarWrapper>
                <SidebarLinks href="#about" onClick={toggle}>A propos</SidebarLinks>
                <SidebarLinks href="#competences" onClick={toggle}>Competences</SidebarLinks>
                <SidebarLinks href="#portfolio" onClick={toggle}>Portfolio</SidebarLinks>
                <SidebarLinks href="#contact" onClick={toggle}>Contact</SidebarLinks>
            </SidebarWrapper>
        </Sidebarcontainer>
    )
}

export default Sidebar