import React from 'react';
import styled from 'styled-components';
import TypeWriter from "typewriter-effect";

const HomePage = styled.section`
  height: 100vh;
  width: 90vw;
  margin: 0 auto;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: 40% 60%;
  align-items: center;
  justify-content: center;

    @media (min-width: 768px) {
        grid-template-rows: auto;
        grid-template-columns: 60% 40%;
    }
`

const TextSection = styled.div`
  width: 100%;
  align-self: flex-end;
  
    @media (min-width: 768px) {
        align-self: center;
    }`

const DevText = styled.p`
  text-transform: uppercase;
  margin-bottom: 20px;
  font-style: oblique;
  
    @media (min-width: 768px) { font-size: 1.8vw; }`

const TextPortfolio = styled.p`
  color: black;`
  
const SeePortfolio = styled.a`
  -webkit-text-decoration: transparent;
          text-decoration: transparent;
  color: red;
  border-bottom: 1px dotted red;`

const ImageSection = styled.div`
  align-self: flex-start;
  justify-self: center;
  width: 30vw;
  -webkit-animation: float 5s ease-in-out infinite;
          animation: float 5s ease-in-out infinite;

    @media (min-width: 768px) {
        align-self: center;
        justify-self: flex-start;
    }`

class HeroElement extends React.Component {
  render() {
    return (
        <HomePage>
          <TextSection>
            <div className="remiLopez">
                <TypeWriter onInit={(typewriter) => {
                    typewriter.typeString('<span>Remi Lpz</span>').start().deleteChars(3).typeString('Lopez').start();
                        }} />
            </div>
            <DevText>Développeur Web Junior</DevText>
            <TextPortfolio>Pour découvrir mon travail, <SeePortfolio href="#portfolio">cliquez ici</SeePortfolio> !</TextPortfolio>
          </TextSection>
          <ImageSection>
            <img className="portrait" src={process.env.PUBLIC_URL + '../img/illu-remi.png'} alt="Portrait" />
          </ImageSection>
        </HomePage> 
    )
  }
}

export default HeroElement