import React, {useState} from 'react';
import Navbar from '../Navbar/headerNav';
import Sidebar from '../Navbar/headerMenu';

const Home = () => {
    const [isOpen, setIsOpen] = useState(false);

    const toggle = () => {
        setIsOpen(!isOpen)
    }

    return (
        <>
            <Sidebar isOpen={isOpen} toggle={toggle}/>
            <Navbar toggle={toggle} />
        </>
    );
};

export default Home